import ReactApexChart from "react-apexcharts";
import ChartOne from "./ChartOne";
import ChartTwo from "./ChartTwo";
import ChartThree from "./ChartThree";
import { isMobile } from "react-device-detect";

// import * as d3 from "d3";
// import Pie from "./D3PieCharts";
// import { useState } from "react";

const CustomCharts = props => {
    const { orientation, series, labels, config, title, type, width, height } = props;

    return <>

        {orientation === 'Line' && <ChartOne
            series={series}
        />}

        {orientation === 'Column' && <ChartTwo
            series={series}
        />}

        {orientation === 'Donut' && <ChartThree
            title={title}
            series={series}
            labels={labels}
        />}
        {orientation === 'Bar' &&
            <div className="mb-2">
                <div id="reactApexChart" className="mx-auto flex justify-center">
                    <ReactApexChart
                        options={config}
                        series={series}
                        type={type}
                        width={width}
                        height={height}
                    />
                </div>
            </div>
        }
        {orientation === 'Custom' &&
            <div className="mb-2">
                <div id="reactApexChart" className="mx-auto flex justify-center">
                    <ReactApexChart
                        options={config}
                        series={series}
                        type={type}
                    />
                </div>
                {/* <div className="App">
                    <div className="piechart">
                        <Pie
                            data={data}
                            width={300}
                            height={300}
                            innerRadius={80}
                            outerRadius={120}
                        />
                    </div>
                </div> */}
            </div>
        }
    </>
}

export default CustomCharts;